import React, { useEffect } from "react";
import styles from "./about.module.css";
import { SliderAll } from "../../Components";
import settings from "./slider";
import { Container, Col, Row } from "react-bootstrap";
import { Link } from "react-router-dom";
import { projectAction } from "../../redux/actions/projectAction";
import { useTranslation } from "react-i18next";
import footerampik from "../../svg/Ampiknerfooter.png"
import {
  aboutAction,
  aboutBannerAction,
} from "../../redux/actions/aboutAction";
import { useSelector, useDispatch } from "react-redux";
import stylesa from "../Home/home.module.css"
import garshok from "../../svg/garshok.png"

const About = () => {
  const dispatch = useDispatch();
  const { t } = useTranslation();

  useEffect(() => {
    dispatch(aboutAction());
    dispatch(aboutBannerAction());
    dispatch(projectAction());
  }, []);

  const data = useSelector((state) => state.aboutReducer.about);
  const banner = useSelector((state) => state.aboutReducer.banner);
  const project = useSelector((state) => state.projectReducer.project);

  const ABOUTE_SLIDER = banner?.map((i) => {
    return (
      <div key={i.id} className={stylesa.best_sale_box}>
      <div className={stylesa.sal_img_blco}>
          <h3 className={stylesa.title_slider}>
              <b>{localStorage.getItem("lang") == "am" ? 'Պահպանեք շրջակա միջավայրն այսօր ավելի լավ վաղվա օրվա համար'
               : 'Save the environment today for a better tomorrow'}</b>
          </h3>
          <div className={stylesa.blackbutt}> 
                    <Link to={`/`}>
                        <button >{t('seeMore')}</button>
                     </Link>
                     </div>
          <img src={footerampik} alt=""/>
          <div className={stylesa.asd}> <img src={garshok} alt=''/> </div>
          <div className={stylesa.subtitle_blcok}>
              <h4 className={stylesa.subtitle_slider}>23,800 trees planted</h4>
              <div className={stylesa.line}></div>
          </div>
             
      </div>
  </div>
    );
  });

  return (
    <>
      <section className={styles.section}>
        <div className={styles.mainDiv}>
          <SliderAll settings={settings} ABOUTE_SLIDER={ABOUTE_SLIDER} />
        </div>
      </section>
      <section>
        <Container>
          <Row>
            <Col sm={12} md={6} lg={6}>
              <div className={styles.name_two_block}>
                <div className={styles.line_two}></div>
                <h4 className={styles.just_name}><b>{t("aboutUs").toUpperCase()}</b></h4>
              </div>
              <h1 className={styles.title_two}>
                <b>{localStorage.getItem("lang") == "am"
                  ? 'Մենք շահույթ չհետապնդող թիմ ենք, որն աշխատում է ամբողջ աշխարհում'
                  : 'We are a nonprofit team working worldwide'}</b>
              </h1>
              <p className={styles.text_two}>
                {localStorage.getItem("lang") == "am"
                  ? data?.descriptionHy
                  : data?.descriptionEn}
              </p>
            </Col>
            <Col sm={12} md={6} lg={6}>
              <div className={styles.img_two}>
                <img src={data?.image} alt="" />
              </div>
            </Col>
          </Row>
        </Container>
      </section>
      <section className={styles.section_two}>
        <Container>
          <Row>
            <Col sm={12} md={6} lg={6} >
              <h1><b>{localStorage.getItem("lang") == "am" ? 'Մեր առաքելությունը' : 'Our Mission'}  </b></h1>
              <h3 style={{paddingRight: 30}} > {localStorage.getItem("lang") == "am" ? 'Մենք ստեղծում ենք աշխարհ՝ փրկելով մեր սեփական միջավայրը' : 'We make the world we save our own environment'}</h3>
              <p style={{paddingRight: 30}}>Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                 Suspendisse varius enim in eros elementum tristique. Duis
                  cursus, mi quis viverra ornare, eros dolor interdum nulla, ut commodo
                   diam libero vitae erat. Aenean faucibus nibh et justo cursus id rutrum
                    lorem imperdiet. Nunc ut sem vitae risus tristique posuere.</p>
            </Col>
            <Col sm={12} md={6} lg={6} >
            <h1><b>{localStorage.getItem("lang") == "am" ? 'Մեր առաքելությունը' : 'Our Mission'}  </b></h1>
              <h3 style={{paddingRight: 100}}>{localStorage.getItem("lang") == "am" ? 'Ավելի շատ ծառեր տնկեք՝ աշխարհի օդի աղտոտվածությունից ազատվելու համար' : 'Plant more trees to make world air pollution free'} </h3>
              <p >Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                 Suspendisse varius enim in eros elementum tristique. Duis
                  cursus, mi quis viverra ornare, eros dolor interdum nulla, ut commodo
                   diam libero vitae erat. Aenean faucibus nibh et justo cursus id rutrum
                    lorem imperdiet. Nunc ut sem vitae risus tristique posuere.</p>
            </Col>
          </Row>
        </Container>
      </section>
      <section>
        <Container>
          <Row>
            <div className={styles.proect_block}>
              <div className={styles.project_name_block}>
                <div className={styles.project_line}></div>
                <h5 className={styles.project_name}>{t("proj")}</h5>
              </div>
              <h5 className={styles.project_name_two}><b>{t("aboutTiT")}</b></h5>
            </div>
            {project
              ? project.slice(0, 3).map((i) => {
                  return (
                    <Col sm={12} md={6} lg={4} key={i.id} className="mb-5">
                      <div
                        className={styles.card}
                        style={{
                          height: "400px",
                          backgroundImage: `url(${i.image})`,
                          backgroundSize: "cover",
                          backgroundPosition: "50% 50%",
                          backgroundRepeat: "no-repeat",
                          borderRadius: "20px",
                        }}
                      >
                        <h3 className={styles.card_name}>
                          {localStorage.getItem("lang") == "am"
                            ? i.titleOneHy
                            : i.titleOneEn}
                        </h3>
                        <p className={styles.text}>
                          {localStorage.getItem("lang") == "am"
                            ? i.descriptionOneHy.substring(0, 30)
                            : i.descriptionOneEn.substring(0, 30)}
                        </p>
                        <Link to={`/details/${i.id}`}>
                          <button className={styles.see_more}>{t('seeMore')}</button>
                        </Link>
                      </div>
                    </Col>
                  );
                })
              : null}
          </Row>
        </Container>
      </section>
    </>
  );
};

export default About;
